<template>
	<view class="page bgwhite">
		<view class="gaugeWrap">
			<qiun-data-charts type="gauge" :opts="opts" :chartData="chartData" canvas2d />
		</view>
		<view class="radius btn oee">OEE/{{ $t('总生产效率') }}</view>
		<view class="radius btn process" v-for="item in list" :key="item.name" @click="itemClick(item)">
			<view class="line" :style="'width:' + item.value + '%'"></view><text>{{ item.name }} {{ item.value }}%</text>
		</view>
	</view>
</template>

<script>

export default {
	data() {
		return {
			list: [{
				name: this.$t('开机率'),
				value: 0
			},
			{
				name: this.$t('周期率'),
				value: 0
			},
			{
				name: this.$t('良品率'),
				value: 0
			},
			{
				name: this.$t('设备利用率'),
				value: 0
			},
			],
			chartData: {},
			opts: {
				color: ["#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4",
					"#ea7ccc"
				],
				padding: undefined,
				title: {
					name: "0%",
					fontSize: 20,
					color: "#2fc25b",
					offsetY: 50
				},
				subtitle: {
					name: undefined,
					fontSize: 15,
					color: "#666666",
					offsetY: -50
				},
				extra: {
					gauge: {
						type: "default",
						width: 10,
						labelColor: "#666666",
						startAngle: 0.75,
						endAngle: 0.25,
						startNumber: 0,
						endNumber: 100,
						labelFormat: "",
						splitLine: {
							fixRadius: 0,
							splitNumber: 10,
							width: 20,
							color: "#fff",
							childNumber: 5,
							childWidth: 10
						},
						pointer: {
							width: 14,
							color: "auto"
						}
					}
				}
			}
		};
	},
	onLoad(options) {
		console.log("传过来的数据=", JSON.parse(options.detail))
		let dataList = JSON.parse(options.detail)
		if (dataList.length > 0) {
			this.getServerData(dataList[0].value)
			this.$nextTick(() => {
				this.list.forEach((item, index) => {
					if (dataList.length > index + 1) {
						item.value = Number(dataList[index + 1].value * 100).toFixed(1)
					}
				})
			})
		}
	},
	methods: {
		getServerData(data) {
			if (data != null) {
				setTimeout(() => {
					//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
					let res = {
						categories: [{
							"value": 0.2,
							"color": "#228b22"
						}, {
							"value": 0.8,
							"color": "#4488bb"
						}, {
							"value": 1,
							"color": "#ff4500"
						}],
						series: [{
							name: "OEE/" + this.$t('总生产效率'),
							data: data
						}]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
					this.opts.title.name = Number(data*100).toFixed(1) + "%"
				}, 500);
			}
		},
		//跳转详情
		itemClick(item) {
			uni.navigateTo({
				url: "/pages/machine/oeeDetailItem?value=" + item.value/100 + "&name=" + item.name,
			})
		},
	}
}
</script>

<style lang="scss" scoped>
.gaugeWrap {
	width: 100%;
	height: 430upx;
	padding-top: 30upx;
}

.btn {
	background-color: #eac80d;
	text-shadow: 0px 1px 3px rgba(80, 39, 181, 0.75);
	color: #fff;
}

.oee {
	width: 333upx;
	height: 94upx;
	line-height: 94upx;
	text-align: center;
	font-size: 36upx;
	margin: 0 auto 66upx;
}

.radius {
	border-radius: 20upx;
}

.process {
	margin: 0px 106upx 46upx;
	height: 102upx;
	position: relative;
	overflow: hidden;

	text {
		position: absolute;
		z-index: 3;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		line-height: 102upx;
		text-align: center;
	}

	.line {
		position: absolute;
		height: 100%;
		left: 0;
		top: 0;
		z-index: 2;
		width: 0;
		transition: width 1s ease-in;
		background-image: -moz-linear-gradient(0deg, rgb(26, 188, 156) 0%, rgb(25, 168, 140) 47%, rgb(24, 147, 123) 100%);
		background-image: -webkit-linear-gradient(0deg, rgb(26, 188, 156) 0%, rgb(25, 168, 140) 47%, rgb(24, 147, 123) 100%);
		background-image: -ms-linear-gradient(0deg, rgb(26, 188, 156) 0%, rgb(25, 168, 140) 47%, rgb(24, 147, 123) 100%);
	}
}
</style>